body {
  margin: 0;
  padding: 0;
}

.header {
  margin-left: 98px;
  margin-right: 98px;
  margin-top: 30px;
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  grid-template-areas: "search logo user" "nav nav nav";
}
@media (max-width: 1023px) {
  .header {
    margin: 0;
    padding-top: 30px;
    padding-left: 45px;
    padding-right: 45px;
    min-height: 126px;
  }
  .header--closed {
    display: flex;
    box-sizing: border-box;
  }
  .header--opened {
    display: flex;
    flex-direction: column;
    min-height: 778px;
    background-color: #a87b62;
  }
}
@media (max-width: 767px) {
  .header {
    padding-top: 20px;
    padding-left: 15px;
    padding-right: 15px;
    min-height: 92px;
  }
  .header--opened {
    min-height: 684px;
  }
}

.header__btn {
  display: none;
  outline: none;
  border: none;
}
@media (max-width: 1023px) {
  .header__btn {
    display: block;
    height: 18px;
    width: 25px;
    position: relative;
    cursor: pointer;
    background: transparent;
    margin-top: 25px;
  }
  .header__btn::before {
    content: "";
    position: absolute;
    background: #1d1613;
    box-shadow: 0 6px #1d1613, 0 12px #1d1613;
    height: 2px;
    width: 25px;
    top: 0;
    left: 0;
  }
}
@media (max-width: 767px) {
  .header__btn {
    margin-top: 18px;
  }
}

.header label {
  grid-area: search;
  position: relative;
}
.header label::before {
  content: "";
  position: absolute;
  width: 14px;
  height: 14px;
  background-image: url("../img/search.svg");
  left: 0;
  top: 3px;
}
@media (max-width: 1023px) {
  .header label {
    display: none;
  }
}

.header input {
  border: none;
  outline: none;
  padding-left: 29px;
}
.header input::placeholder {
  font-family: "Poppins", "Arial", sans-serif;
  font-size: 14px;
  color: #9b9997;
}
@media (max-width: 1023px) {
  .header input {
    width: 100%;
    height: 40px;
    border-radius: 21px;
    background-color: rgba(255, 255, 255, 0.15);
  }
  .header input::placeholder {
    visibility: hidden;
  }
}

.header svg {
  grid-area: logo;
  justify-self: center;
  margin-bottom: 37px;
}
@media (max-width: 1023px) {
  .header svg {
    position: absolute;
    top: 30px;
    left: 50%;
    transform: translateX(-50%);
    margin-bottom: 0;
  }
  .header svg path {
    fill: #a87b62;
  }
}
@media (max-width: 767px) {
